import React, {useState} from "react";
import Swal from "sweetalert2";
import ScrollTopAction from "./Action/ScrollTopAction";
import ContactUsBanner from "./Contact Us/ContactUsBanner";
import ContactUsLeftSide from "./Contact Us/ContactUsLeftSide";
import ContactUsRightSide from "./Contact Us/ContactUsRightSide";

function ContactUsPage(props){
    document.title = "HM Shop | Contact Us"
    let [name, setName] = useState("");
    let [email, setEmail] = useState("");
    let [message, setMessage] = useState("");
    let [error, setError] = useState("");

    let send = (e)=>{
        e.preventDefault();
        ScrollTopAction();
        let TextError = [];
        let count = 0;
        if(name.length < 3){
            TextError.push("The name must be at least 3 characters.");
            count++;
        }
        if(email.search("@") > -1 && email.lastIndexOf(".") > -1 && (email.indexOf("@") < email.lastIndexOf("."))){
            console.log(email.search("@"));
        }else{
            TextError.push("The email must be a valid email address.");
            count++;
        }
        if(message.length < 5){
            TextError.push("The message must be at least 5 characters.");
            count++;
        }
        if(count > 0){
            
                setError(TextError);
           
        }else{
          setError([]);
          Swal.fire({
              position: 'center',
              icon: 'success',
              title: 'Message sent',
              showConfirmButton: false,
              timer: 1500
            })
          setName("");
          setEmail("");
          setMessage("");
        }
    }
    return(
        <div className="container mx-auto px-2 xl:px-0 flex flex-col">
        <ContactUsBanner />
       <div className="w-full px-2 lg:px-0 my-2 flex flex-col rounded min-h-fit lg:min-h-[472px] xl:min-h-[532px]">

       <div className="mt-2 mb-1 px-0 lg:px-2 xl:px-0">
            <div className={`w-full relative bg-red-500 text-white py-2 px-4 mb-2 rounded ${error.length ? "block" : "hidden"}`}>
                <ol className=" list-inside list-decimal">
                {error.length ? error.map((item, key)=><li key={key} className="my-2">{item}</li>) : null}
                </ol>
            </div>
            </div>

           <div className="flex flex-col lg:flex-row">
             
             <ContactUsLeftSide name={name} setName={setName} email={email} setEmail={setEmail} message={message} setMessage={setMessage} send={send} />
             <ContactUsRightSide />
         </div>
         
       </div>
      </div>
    )
}

export default ContactUsPage;